.sidebar {
  position: fixed;
  display: none;
}

/* .sidebar.open {
  display: flex;
  justify-content: center;
} */

.open {
  width: 317px;
  height: 100vh;
  display: flex;
  justify-content: left;
  flex-direction: column;
  background-color: #ffffff;
  z-index: 100;
  /* height: 100vh; */
}

.background {
  background: rgba(0, 0, 0, 0.3);
  position: fixed;
  z-index: 5;
  /* width: 600px; */
  width: 100%;
  height: 100%;
  /* opacity: 0.3; */
}

.nav {
  flex-direction: column;
}

.nav a {
  display: flex;
  justify-content: center;
  text-decoration: none;
  font-size: 22px;
  color: #000000;
  margin-top: 45px;
  /* font-family: mon; */
}

.nav a:hover {
  color: #58acff;
}

.nav a.active {
  text-decoration: underline;
  color: #0059b2;
}
.close {
  /* width: 317px; */
  display: block;
  width: fit-content;
  height: fit-content;
  margin-left: 90%;
  /* position: relative; */

  /* right: 0%; */
  border: none;
  background-color: transparent;
}

